import React, { Suspense } from 'react'
import { Img } from 'react-image'
import id8logo from '../../images/logos/ID8-Logo.png'
import id8tag from '../../images/logos/iD8Tagline-01.png'
import {
    Collapse,
    Navbar,
    Button,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
} from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSlack, faFacebook } from '@fortawesome/free-brands-svg-icons'
import { Link } from 'gatsby'

const NavbarContent = () => {
    return (
        <Navbar color="light" light expand="md" className="navbar">
            <a href="/">
                <Img src={id8logo} height="100" />
                <Img src={id8tag} height="50" />
            </a>
            <NavbarToggler aria-controls="responsive-navbar-nav" />
            <Collapse id="responsive-navbar-nav" navbar>
                <Nav className="mr-auto" navbar>
                    <NavItem>
                        <NavLink>
                            <Link to="/aboutus">About Us</Link>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink>
                            <Link to="/blog">Blog</Link>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink>
                            <Link to="/projects">Our Projects</Link>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink>
                            <Link to="/team">Our Team</Link>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink>
                            <Link to="/guides"> Resources Guides</Link>
                        </NavLink>
                    </NavItem>
                </Nav>
                <Nav>
                    <NavItem>
                        <NavLink href="https://app.slack.com/client/TCFHZAZ0A/GE5M03762">
                            <FontAwesomeIcon icon={faSlack} />
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink href="https://www.facebook.com/groups/jcumvpclub/">
                            <FontAwesomeIcon icon={faFacebook} />
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink eventKey={2} href="/signup">
                            Sign Up
                        </NavLink>
                    </NavItem>
                </Nav>
            </Collapse>
        </Navbar>
    )
}

export default NavbarContent
